.navigation {
  display: flex;
  font-size: 2rem;
  background: #4a444e;
  position: fixed;
  width: 100%;
  transition-property: top;
  transition-duration: .6s;
  transition-delay: .2s;
  z-index: 1;
  padding: 0.3rem 0;
}

.navigation .navigation-empty-first {
  flex: 0.35 1;
}

.navigation .navigation-item {
  margin: 0 2rem;
  text-decoration: none;
  color: #b38e9c;
}

.navigation .navigation-item .navigation-item-text {
  cursor: pointer;
  letter-spacing: 2px;
  text-transform: uppercase;
  text-shadow: 0 0 2px #b38e9c;
}

.navigation .navigation-food-menu {
  position: relative;
}

.navigation .navigation-food-menu .navigation-food-menu-list {
  cursor: context-menu;
  position: absolute;
  visibility: hidden;
  right: -4rem;
}

.navigation .navigation-food-menu .navigation-food-menu-list .navigation-food-menu-list-shape {
  width: 15rem;
  background: #5a5077;
}

.navigation .navigation-food-menu .navigation-food-menu-list .navigation-food-menu-list-shape .navigation-categories-list {
  margin-top: 1rem;
  padding: 0.5rem 0.3rem;
  display: flex;
  flex-direction: column;
  text-align: center;
}

.navigation .navigation-food-menu .navigation-food-menu-list .navigation-food-menu-list-shape .navigation-categories-list a {
  text-decoration: none;
  color: #7e7b7c;
  text-shadow: 1px 1px 1px #473f43;
  letter-spacing: 1px;
}

.navigation .navigation-food-menu .navigation-food-menu-list .navigation-food-menu-list-shape .navigation-categories-list a:hover {
  background: #9686c5;
  color: #473f43;
}

.navigation .navigation-food-menu .navigation-food-menu-list .navigation-food-menu-list-shape::before {
  position: absolute;
  top: 0;
  left: 40%;
  content: ' ';
  border: solid;
  border-color: #5a5077 transparent;
  border-width: 0 2rem 1rem 2rem;
}

.navigation .navigation-food-menu:hover .navigation-food-menu-list {
  visibility: visible;
}

.navigation-little-visible {
  top: 0;
  opacity: 0.3;
}

.navigation-hidden {
  opacity: 0.3;
  top: -5rem;
}
