.home .category .category-line .category-line-inner4 {
  background-image: url(/restaurant3578/static/media/full-menu.d3a5de2c.jpeg);
}

.home .category .category-line > *::before,
.home .category .category-line > *::after {
  transition: -webkit-transform .5s ease-in-out;
  transition: transform .5s ease-in-out;
  transition: transform .5s ease-in-out, -webkit-transform .5s ease-in-out;
}

.home .category .category-line > *::before {
  content: '';
  position: absolute;
  height: calc(100% - 6px);
  width: calc(50% - 6px);
  border-top: 3px solid #ffffff;
  border-bottom: 3px solid #ffffff;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
}

.home .category .category-line > *::after {
  content: '';
  position: absolute;
  height: 100%;
  width: calc(50% - 6px);
  border-left: 3px solid #ffffff;
  border-right: 3px solid #ffffff;
  -webkit-transform: scaleY(0);
          transform: scaleY(0);
}

.home .category .category-line > *:hover {
  opacity: .7;
}

.home .category .category-line > *:hover::before {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}

.home .category .category-line > *:hover::after {
  -webkit-transform: scaleY(1);
          transform: scaleY(1);
}

